import { Button, Stack } from '@mui/material'
import { FC } from 'react'
import { useNavigate } from 'react-router-dom'

interface CancelSubmitBtnsProps {
  handleSubmit: (event) => void
  handleCancel?: (event) => void
  submitBtnText?: string
}

const CancelSubmitBtns: FC<CancelSubmitBtnsProps> = ({
  handleSubmit,
  handleCancel,
  submitBtnText
}: CancelSubmitBtnsProps) => {
  const navigate = useNavigate()

  const _handleCancel = event => {
    if (handleCancel === undefined) navigate(-1)
    else handleCancel(event)
  }

  return (
    <Stack mt={2} pb={2} direction="row" spacing={2} justifyContent="center">
      <Button color="error" variant="outlined" sx={{ width: '50%', maxWidth: '120px' }} onClick={_handleCancel}>
        취소
      </Button>

      <Button
        color="success"
        variant="contained"
        onClick={handleSubmit}
        sx={{ width: '50%', maxWidth: '120px', padding: '0px' }}
      >
        {submitBtnText || '등록'}
      </Button>
    </Stack>
  )
}

export default CancelSubmitBtns
