import MobileDatePicker from '@mui/lab/MobileDatePicker'
import { Box, Card, Container, Grid, TextField } from '@mui/material'
import axios, { AxiosResponse } from 'axios'
import dayjs, { Dayjs } from 'dayjs'
import { useSnackbar } from 'notistack'
import { FC, useEffect, useRef, useState } from 'react'
import { useAuthHeader, useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'
import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import TextEditor from 'src/components/TextEditor'
import PageHeader from '../PageHeader'

interface RecruitAPIResponse {
  contents: string
  startAt: string
  semester?: string
  endAt?: string
  limit?: number
  instagramId?: string
}

const Intro: FC<void> = () => {
  const authUser = useAuthUser()
  const params = useParams()
  const clubId = parseInt(params.id)
  const club = authUser()?.clubs.filter(c => c.id === clubId)[0]
  const authHeader = useAuthHeader()

  const [startAt, setStartAt] = useState<Dayjs | null>(dayjs('2023-01-01T00:00:00'))
  const [endAt, setEndAt] = useState<Dayjs | null>(dayjs('2023-01-01T00:00:00'))
  const [limit, setLimit] = useState<number>(0)
  const [instagramId, setInstagramId] = useState<string>('')
  const [isEditMode, setIsEditMode] = useState<boolean>(false)
  const { enqueueSnackbar } = useSnackbar()
  const editorRef = useRef<any>(null)

  const handleSubmit = () => {
    if (endAt.isBefore(startAt)) {
      enqueueSnackbar('종료일이 시작일보다 빠를 수 없습니다.', { variant: 'error' })
      return
    }

    // 모집 종료일 기준으로 학기 나눔
    axios({
      method: isEditMode ? 'put' : 'post',
      url: `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit`,
      data: {
        contents: editorRef.current?.getInstance().getHTML(),
        startAt: startAt,
        endAt: endAt,
        limit: limit || 0,
        instagramId: instagramId
      },
      headers: { Authorization: authHeader() }
    })
      .then(res => {
        if (res.data.message === 'ok') {
          enqueueSnackbar('등록이 완료되었습니다.', { variant: 'success' })
        } else {
          throw new Error()
        }
      })
      .catch(() => {
        enqueueSnackbar('등록에 실패했습니다. 다시 시도하여 주십시오.', { variant: 'error' })
      })
  }

  useEffect(() => {
    // TODO : url 뒤에 / 떼면 301 Moved Permanently 에러뜸
    axios
      .get(`${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/`)
      .then((res: AxiosResponse<RecruitAPIResponse>) => {
        setStartAt(dayjs(res.data.startAt).startOf('date'))
        setEndAt(dayjs(res.data.endAt).startOf('date'))
        setLimit(res.data.limit)
        setInstagramId(res.data.instagramId)
        setIsEditMode(true)

        editorRef.current?.getInstance().setHTML(res.data.contents)
      })
  }, [])

  return (
    <>
      <Helmet>
        <title>소개 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club.name} link={`/manage/${clubId}/attendance/register`} />
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <Card>
          <TextEditor
            initialValue=""
            editorRef={editorRef}
            toolbarItems={[
              ['heading', 'bold', 'italic', 'strike'],
              ['hr', 'quote'],
              ['ul', 'ol', 'task'],
              ['table', 'image', 'link'],
              ['code', 'codeblock']
            ]}
          />
        </Card>
        <Box mt={2} mb={2}>
          <Card sx={{ padding: { xs: '20px', md: '30px' } }}>
            <Grid container justifyContent="center" spacing={2}>
              <Grid item sm={12} md={3}>
                <MobileDatePicker
                  label="모집 시작일"
                  inputFormat="yyyy/MM/dd"
                  value={startAt}
                  onChange={newValue => {
                    setStartAt(dayjs(newValue))
                  }}
                  renderInput={params => <TextField {...params} />}
                />
              </Grid>
              <Grid item sm={12} md={3}>
                <MobileDatePicker
                  label="모집 종료일"
                  inputFormat="yyyy/MM/dd"
                  value={endAt}
                  onChange={newValue => {
                    setEndAt(dayjs(newValue))
                  }}
                  renderInput={params => <TextField {...params} />}
                />
              </Grid>
              <Grid item sm={12} md={3}>
                <Box component="form">
                  <TextField
                    label="인원 제한(없으면 0)"
                    id="limit"
                    value={limit}
                    type="number"
                    onChange={e => {
                      setLimit(parseInt(e.target.value))
                    }}
                  ></TextField>
                </Box>
              </Grid>
              <Grid item sm={12} md={3}>
                <TextField
                  label="동아리 인스타그램 ID"
                  id="instagramId"
                  value={instagramId}
                  onChange={e => {
                    setInstagramId(e.target.value)
                  }}
                ></TextField>
              </Grid>
            </Grid>
          </Card>
          <CancelSubmitBtns handleSubmit={handleSubmit} submitBtnText="등록 / 수정" />
        </Box>
      </Container>
    </>
  )
}

export default Intro
